import _ from 'lodash';

// pathhttphttps
export const replaceURLPublicPath = (path) => {
  if (!path) return;
  if (_.includes(path, 'http') || _.includes(path, 'https')) {
    return path;
  } else {
    if (path.slice(0, 1) === '/') {
      // 
      return process.env.PUBLIC_URL + path;
    } else {
      return process.env.PUBLIC_URL + '/' + path;
    }
  }
}